.hero{   
    display: flex;
    flex-direction: column;    
    width: 100%;
    min-height: 100vh;
    background: rgba(255,255,255,1);
    background: -moz-radial-gradient(center, ellipse cover, rgba(255,255,255,1) 0%, rgba(211,211,211,1) 100%);
    background: -webkit-gradient(radial, center center, 0px, center center, 100%, color-stop(0%, rgba(255,255,255,1)), color-stop(100%, rgba(211,211,211,1)));
    background: -webkit-radial-gradient(center, ellipse cover, rgba(255,255,255,1) 0%, rgba(211,211,211,1) 100%);
    background: -o-radial-gradient(center, ellipse cover, rgba(255,255,255,1) 0%, rgba(211,211,211,1) 100%);
    background: -ms-radial-gradient(center, ellipse cover, rgba(255,255,255,1) 0%, rgba(211,211,211,1) 100%);
    background: radial-gradient(ellipse at center, rgba(255,255,255,1) 0%, rgba(211,211,211,1) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ffffff', endColorstr='#d3d3d3', GradientType=1 );
    overflow: hidden;    
    @include dosk{
        min-height: 60vh;
    }
}
.hero-content{
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    flex: 1;
    @include desktop{
        display: grid;
        gap: 2rem;
        grid-template-columns: repeat(2, 50%);
        place-items: center;
    }        
    .hero__left{
        flex: 1;
        height: 100%;
        width: 100%;
        display: flex;
        flex-direction: column;
        z-index: 1;      
        h1{
            font-size: 6rem;
            text-align: center;
            line-height: 0.8;
            margin-bottom: 2rem;            
            @include tablet{
                font-size: 10rem;
            }
            @include desktop{
                font-size: 12rem;
                text-align: left;
            }           
            span {
                display: block;
                font-size: 4.5rem;
                font-weight: $light;
                @include tablet{
                    font-size: 7.5rem;
                }
                @include desktop {
                    font-size: 9rem;
                }
            }
        }
        p.slogan{
            font-weight: $normal;
            line-height: 1;
            text-align: center;
            @include tablet {
                font-size: 3rem;
            }
            @include desktop{
                text-align: left;
            }
        }
        .button{
            margin-top: 4rem;
            @include desktop{
                margin: 8rem 0 0 0;
            }
        } 
    }
    .hero__right{ 
        padding: 4rem 0; 
        height: 100%;
        width: 100%;
        display: flex;                              
        justify-content: center;
        align-items: center;
        flex: 1;
        @include tablet{
            padding: 0;
        }    
        img{
            width: 22rem; 
            height: 22rem;
            @include tablet{
                width: 50rem;
                height: 48rem;                
            }
            @include desktop{
                width: 60rem;
                height: 60rem;
                transform: translateX(-6rem);
            }
        }
    }
}