.container{
    width: 96%;
    max-width: 120rem;
    margin: 0 auto;
}
.no-scroll{
    overflow: hidden;
}
.text-center{
    text-align: center;
}
.button{
    position:relative;
    height: 6rem;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;    
    color: $blanco;
    font-family: $titulos;
    font-weight: 500;
    font-size: 2.5rem;
    text-align: center;
    cursor: pointer;
    transition: all .3s ease-in-out;
    -webkit-box-shadow: 0px 2px 4px 0px rgba(0,0,0,0.5); 
    box-shadow: 0px 2px 4px 0px rgba(0,0,0,0.5);   
    @include telefono {
        width: 50%;
        margin: 0 auto;
    }
    @include tablet{
        width: 20rem;        
    }
    @include desktop{
        width: 30rem;
        
    }
}
.btn-primary{
    background-color: $primario;
    &:hover{
        background-color: $primarioClaro;;
    }
}
.btn-secundary{
    background-color: $secundario;
    border-radius: 3.5rem;
    font-family: $contenido;
    font-weight: $light;
    font-size: 1.8rem;
    height: 5rem;
    width: 70%;
    margin: 0 auto;
    &:hover{
        background-color: $secundarioClaro;
    }
}
.new-section{
    margin-top: $separacion/2;
}
.titulo{
    text-transform: uppercase;
    margin: $separacion/2 0;
    padding-bottom: .4rem;
    position: relative;
    display: inline-block;
    &::before{
        content: '';
        position: absolute;
        height: 0;
        width: 50%;
        bottom: 0;
        left: 0;
        right: 0;
        border-bottom: .7rem solid $secundario;
    }
    &::after{
        content: '';       
        position: absolute;
        height: 0;
        width: 50%;
        bottom: 0;
        right: 0;
        border-bottom: .7rem solid $primario;
    }
}

